.record {
  height: 100%;
  min-height: 100%;
  padding: 5rem;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: flex-start;

  &-loader {
    border: 1rem solid #f3f3f3;
    border-radius: 50%;
    border-top: 1rem solid #3498db;
    width: 8rem;
    height: 8rem;
  }
}

.audio-recorder {
  height: 100%;
  min-width: 40rem;
  width: 40rem;
  max-width: 100%;
  border: 1px solid #ddd;
  border-radius: .5rem;
  background-color: var(--color-secondary);
  background-image: linear-gradient(-230deg, var(--color-primary), var(--color-secondary));
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10rem;
  
  &__duration {
    height: 17rem;
    width: 17rem;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    flex-shrink: 0;
    align-items: center;
    font-size: 3.5rem;
    letter-spacing: 1px;
    background-color: #fff;
    color: #555;
  }

  &__controls {
    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: 5rem;
  }

  &__btn {
    border-radius: 100%;
    background-color: #fff;
    border: none;
    height: 5rem;
    width: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    cursor: pointer;

    &--stop {
      margin-left: 3rem;
    }
  }

  &__icon {
    height: 2.2rem;
    width: 2.2rem;
    fill: #444;
    
    &--mic {
      margin-left: .2rem;
    }
  }
}
