:root {
  --nav-height: 7rem;
  
  --color-primary: #5c2a83;
  --color-primary-light:  #8756ad;
  --color-primary-dark: #441446;

  --color-primary-values: 92, 42, 131;
  --color-primary-light-values: 135, 86, 173;
  --color-primary-dark-values: 68, 20, 70;

  --color-secondary: #13C5E9;
  
  --color-secondary-values: 174, 50, 107;
  --color-tertiary-values: 228, 91, 44;

  --gradient-primary: #623f93, #ae326b, #e45b2c;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size:62.5%; //1 rem  = 10x; 10px/16px = 62.5%
}

body {
  font-family: "Source Sans Pro", sans-serif;
}

.main {
  width: 100%;
  height: calc(100vh - var(--nav-height));
  margin-top: var(--nav-height);
}
