.set-password {
  background-image: linear-gradient(var(--color-primary), var(--color-primary-dark));
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  &-box {
    min-width: 40rem;
    background-color: #F2F2F2;
    padding: 5rem;
  }

  &-form {
    &__field {
      &:not(:first-child) {
        margin-top: 1.5rem;
      }
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }

    &__label {
      font-size: 1.5rem;
      font-weight: 600;
      color: var(--color-primary);
    }

    &__error {
      color: #ff3031;
      padding: .5rem 0;
      padding-top: 0;
      font-size: 1.5rem;
    }


    &__input {
      width: 100%;
      height: 4.3rem;
      margin-top: .6rem;
      border: none;
      outline: none;
      background-color: #D9D9D9;
      padding: 1.3rem;
      border-radius: 1px;
      letter-spacing: .3px;

      &::placeholder {
        color: #999;
      }
    }

    &__button {
      background-color: var(--color-primary);
      border: none;
      outline: none;
      width: 12.5rem;
      height: 4.4rem;
      color: #eee;
      letter-spacing: .7px;
      margin-top: 2rem;
      cursor: pointer;
      
      &:disabled {
        background-color: var(--color-primary-light);
        cursor: not-allowed;
      }
    }
  }

  &-note {
    font-size: 1.5rem;
    margin-top: 1.5rem;
    font-weight: 600;
    color: #444;
    padding: 0 .1rem;
  }
}
