.recording-form-container {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.recording-form-wrapper {
  background-color: var(--color-secondary);
  background-image: linear-gradient(-230deg, var(--color-primary), var(--color-secondary));
  min-width: 40rem;
  width: 40rem;
  padding: 5rem;
  border-radius: .5rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 4rem;
}

.recording-form {
  margin-top: 4rem;

  &__field {
    &:not(:first-child) {
      margin-top: 1.5rem;
    }
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  
  &__error {
    color: #eee;
    border: 1px solid #eee;
    padding: .8rem 1rem;
    border-radius: .3rem;
    margin-bottom: .5rem;
    font-size: 1.5rem;
  }

  &__label {
    font-size: 1.5rem;
    font-weight: 600;
    letter-spacing: .5px;
    color: #eee;
  }

  &__input {
    width: 100%;
    height: 4.3rem;
    margin-top: .6rem;
    border: none;
    outline: none;
    background-color: #eee;
    padding: 1.3rem;
    border-radius: 1px;
    letter-spacing: .3px;
    
    &::placeholder {
      color: #999;
      letter-spacing: 1px;
    }
  }
  
  &__textarea {
    width: 100%;
    height: 10rem;
    margin-top: .6rem;
    resize: none;
    border: none;
    outline: none;
    background-color: #eee;
    padding: 1.3rem 1.5rem;
    border-radius: 1px;
    letter-spacing: .3px;

    &::placeholder {
      color: #999;
    }
  }

  &__button {
    background-color: #333;
    border: none;
    outline: none;
    width: 12.5rem;
    height: 4.4rem;
    color: #eee;
    letter-spacing: .7px;
    margin-top: 2rem;
    cursor: pointer;
  
    &:last-child {
      margin-left: 3rem;
      color: #333;
      background-color: #fff;
    }

    &:disabled {
      background-color: var(--color-primary-light);
      cursor: not-allowed;
    }
  }

  &-recording {
    outline: none;
  }
}
