.sentmail {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(var(--color-primary), var(--color-primary-dark));
  display: flex;
  justify-content: center;
  align-items: center;

  &-box {
    width: 40rem;
    min-height: 15rem;
    padding: 3rem 4rem;
    background-color: #fff;
    box-shadow: 0 .5rem 1rem #222;
    border-radius: 1px;
    display: flex;
    align-items: center;
  }
  
  &-message {
    font-size: 1.6rem;
    line-height: 2.3rem;
    text-shadow: 0 0 .5px var(--color-primary);
    letter-spacing: .5px;
    color: var(--color-primary);
  }
}