.section {
  &-content {
    width: 100%;
    display: flex;
  }

  &-heading {
  position: relative;
  font-size: 2.6rem;
  color: #777;
  font-weight: 600;
  display: flex;
  justify-content: center;
  letter-spacing: .5px;
  text-align: center;

    &::after {
      content: "";
      position: absolute;
      bottom: -2rem;
      height: .3rem;
      width: 5rem;
      background-color: #777;
    }

    &--light {
      color: #ddd;

      &::after {
        background-color: #ddd;
      }
    }
  }
}

.section-padding {
  padding: 10rem;

  @media (max-width: 56.25em) {
    padding: 7rem 5rem;
  }

  @media (max-width: 25em) {
    padding: 7rem 2rem;
  }
}

.home {
  height: 100%;
  width: 100%;
  
  &-section {
    display: flex;
    width: 100%;
  }

  &-cta {
    background-color: #ddd;
    color: var(--color-primary);
    text-shadow: 0 0 .4px var(--color-primary);
    letter-spacing: .5px;
    margin-top: 2rem;
    width: 18rem;
    height: 4.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    font-size: 1.5rem;

    &--dark {
      background-color: #555;
      color: #ddd;
      text-shadow: 0 0 .4px #ddd;
    }

    &--primary {
      background-color: var(--color-primary);
      color: #ddd;
      text-shadow: 0 0 .4px #ddd;
    }

    @media (max-width: 50em) {
      width: 20rem;
      height: 5.5rem;
      font-size: 1.8rem;
    }
  }
}

.home-header {
  height: 100%;
  background-color: var(--color-primary);
  background-image: url("./../../assets/img/home-header.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;

  @media (max-width: 75em) {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__left {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    @media (max-width: 75em) {
      order: 1;
      margin-top: 5rem;
      width: 60%
    }
    
    @media (max-width: 53.125em) {
      width: 100%;
    }
  }

  &__heading-primary {
    font-size: 8rem;
    letter-spacing: 3px;
    color: #ddd;
    
    @media (max-width: 53.125em) {
      font-size: 6rem;
    }
    
    @media (max-width: 31.25em) {
      font-size: 5rem;
    }
  }
  
  &__heading-secondary {
    font-size: 4.3rem;
    font-weight: 600;
    letter-spacing: 2.5px;
    margin-top: -1rem;
    color: #ddd;
    
    @media (max-width: 53.125em) {
      font-size: 3.2rem;
    }
    
    @media (max-width: 31.25em) {
      font-size: 2.6rem;
    }
  }


  &__right {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    
    @media (max-width: 75em) {
      width: 40%
    }
    
    @media (max-width: 53.125em) {
      width: 100%;
    }
  }

  &__logo-cover {
    background-image: linear-gradient(20deg, rgba(var(--color-primary-values), 0.8), rgba(var(--color-primary-dark-values), 1));
    border-radius: 100%;
    width: 25rem;
    height: 25rem;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    
    @media (max-width: 50em) {
      width: 20rem;
      height: 20rem;
    }
  }

  &__logo-img {
    height: 15rem;

    
    @media (max-width: 50em) {
      width: 10rem;
      height: 10rem;
    }
  }
}

.home-steps {
  flex-direction: column;
  align-items: center;
  background-color: rgb(245, 245, 245);
  
  &__content {
    justify-content: space-evenly;
    margin-top: 10rem;
    
    @media (max-width: 40.625em) {
      margin-top: 8rem;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
    }
  }
}

.home-step {
  border-radius: 100%;
  height: 18rem;
  width: 18rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(247, 247, 247);
  box-shadow: -.3rem -.3rem .6rem #fff, .3rem .3rem .6rem #ccc;
  
  @media (max-width: 56.25em) {
    height: 15rem;
    width: 15rem;
  }
  
  @media (max-width: 40.625em) {
    &:not(:last-child) {
      margin-bottom: 5rem;

    }
  }

  &__icon {
    height: 4rem;
    fill: var(--color-secondary);
  }

  &__title {
    margin-top: 1.4rem;
    color: var(--color-secondary);
    font-size: 1.6rem;
    letter-spacing: 1px;
    text-shadow: 0 0 1px var(--color-secondary);
  }
}

.home-auditorium {
  position: relative;
  padding-bottom: 0;
  background-image: linear-gradient(105deg, var(--color-primary-dark) 0%, var(--color-primary-dark) 50%, var(--color-primary) 50%);
  
  @media (max-width: 43.75em) {
    background-image: none;
    background-color: var(--color-primary);

    &::after {
      display: none;
    }
  }

  &__content {
    margin-top: 5rem;
    
    @media (max-width: 43.75em) {
      flex-direction: column;
      align-items: center;
      background-image: none;
    }
  }
  
  &__left {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 5rem 10rem 10rem 5rem;
    
    @media (max-width: 43.75em) {
      padding: 5rem;
      padding-bottom: 7rem;
      width: 100%;
      align-items: center;
    }

    @media (max-width: 25em) {
      padding: 5rem 2rem;
    }
  }
  
  &__right {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    
    @media (max-width: 43.75em) {
      order: -1;
      width: 100%;
      margin-top: 2rem;
    }
  }

  &__text {
    color: #ddd;
    text-shadow: 0 0 0.3px #ddd;
    letter-spacing: .5px;
    line-height: 2.5rem;;
    font-size: 1.6rem;
    margin-bottom: 1rem;
    
    @media (max-width: 43.75em) {
      font-size: 2rem;
      line-height: 3rem;
      margin-bottom: 2rem;
    }
  }

  &__img {
    width: 100%;

    @media (max-width: 43.75em) {
      width: 75%;
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: calc(50% - .9rem);
    background-color: #222;
    border: 2px solid #ddd;
    border-radius: 100%;
    height: 1.4rem;
    width: 1.4rem;
  }
}

.home-discussion {
  position: relative;
  
  @media (max-width: 43.75em) {
    background-image: none;

    &::after {
      display: none;
    }
  }

  &__content {
    margin-top: 10rem;
    
    @media (max-width: 43.75em) {
      margin-top: 8rem;
      flex-direction: column;
      align-items: center;
      background-image: none;
    }
  }
  
  &__left {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0rem 5rem;
    
    @media (max-width: 43.75em) {
      margin-top: 1rem;
      padding: 5rem;
      align-items: center;
      width: 100%;
    }
    
    @media (max-width: 25em) {
      padding: 5rem 2rem;
      padding-bottom: 0rem;
    }
  }
  
  &__right {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    
    @media (max-width: 43.75em) {
      order: -1;
      width: 100%;
      margin-top: 0rem;
    }
  }

  &__text {
    color: #555;
    text-shadow: 0 0 .3px #555;
    letter-spacing: .5px;
    line-height: 2.5rem;;
    font-size: 1.6rem;
    
    @media (max-width: 43.75em) {
      font-size: 2rem;
      line-height: 3rem;
      margin-bottom: 3rem;
    }
  }

  &__img {
    width: 80%;

    @media (max-width: 43.75em) {
      width: 100%;
    }
  }
}

.home-record {
  position: relative;
  background-image: linear-gradient(105deg, var(--color-primary-dark) 0%, var(--color-primary-dark) 50%, var(--color-primary) 50%);
  
  @media (max-width: 43.75em) {
    background-image: none;
    background-color: var(--color-primary);

    &::after {
      display: none;
    }
  }

  &__content {
    margin-top: 10rem;
    
    @media (max-width: 43.75em) {
      margin-top: 8rem;
      flex-direction: column;
      align-items: center;
      background-image: none;
    }
  }
  
  &__left {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0rem 5rem;
    
    @media (max-width: 43.75em) {
      margin-top: 1rem;
      align-items: center;
      width: 100%;
    }
    
    @media (max-width: 25em) {
      padding: 0rem 2rem;
    }
  }

  &__img {
    height: 30rem;
    fill: #ddd;

    @media (max-width: 43.75em) {
      padding-left: 1rem;
    }
  }
  
  &__right {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 0rem 8rem;
    
    @media (max-width: 43.75em) {
      margin-top: 1rem;
      padding: 5rem;
      align-items: center;
      width: 100%;
    }
    
    @media (max-width: 25em) {
      padding: 5rem 2rem;
      padding-bottom: 0rem;
    }
  }

  &__text {
    color: #ddd;
    text-shadow: 0 0 .3px #ddd;
    letter-spacing: .5px;
    line-height: 2.5rem;;
    font-size: 1.6rem;
    
    @media (max-width: 43.75em) {
      font-size: 2rem;
      line-height: 3rem;
      margin-bottom: 3rem;
    }
  }
  
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: calc(50% - .9rem);
    background-color: #222;
    border: 2px solid #ddd;
    border-radius: 100%;
    height: 1.4rem;
    width: 1.4rem;
  }
}

.home-auth {
  position: relative;
  background-image: radial-gradient(#fff, #ccc);
  padding: 15rem;

  &__content {
    margin-top: 7rem;
    justify-content: center;
    
    @media (max-width: 43.75em) {
      margin-top: 8rem;
      flex-direction: column;
      align-items: center;
      background-image: none;
    }
  }

  &__cta {
    height: 6rem;
    margin-left: 2rem;
    color: #ddd;
    text-shadow: 0 0 1px #ddd;
    
    @media (max-width: 43.75em) {
      margin-left: 0;
    }
     
    &--signup {
      background-color: var(--color-primary);
    }
    
    &--login {
      background-color: #777;
    }
  }
}

.home-footer {
  background-color: #333;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 2rem;

  &__content {
    margin: 10rem 0;
    
    @media (max-width: 43.75em) {
      margin: 4rem;
    }
  }

  &__logo-img {
    height: 10rem;
  }

  &-nav__list {
    width: 100%;
    display: flex;
    justify-content: center;

    @media (max-width: 43.75em) {
      flex-direction: column;
      align-items: center;
    }
  }

  &-nav__item {
    width: 16rem;
    height: 6rem;
    margin-right: 2rem;
    background-color: #222;
    list-style: none;
    
    @media (max-width: 43.75em) {
      margin-right: 0;
      width: 17rem;

      &:not(:last-child) {
        margin-bottom: 2rem;
      }
    }
  }

  &-nav__link {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
  }
  
  &-nav__icon {
    fill: #ddd;
    height: 1.6rem;
    width: 1.6rem;
    margin-right: 1rem;
    
    @media (max-width: 43.75em) {
      margin-right: 1.5rem;
    }
  }
  
  &-nav__text {
    color: #ddd;
    letter-spacing: .5px;
    font-size: 1.6rem;
    
    @media (max-width: 43.75em) {
      font-size: 1.8rem;
    }
  }
  
  &__copyright {
    font-size: 1.5rem;
    letter-spacing: .5px;
    color: #ddd;
    text-align: center;
    
    @media (max-width: 43.75em) {
      margin-bottom: 2rem;
      font-size: 1.8rem;
    }
  }
}