.nav {
  width: 100%;
  height: var(--nav-height);
  top: 0;
  left: 0;
  position: fixed;
  display: flex;
  padding: 0 2rem;
  align-items: center;
  background-color: #fff;
  // box-shadow: 0 0 1rem #555;
  border-bottom: 1px solid #ddd;
  z-index: 100;
  
  &__toggler {
    position: relative;
    height: .2rem;
    width: 2.8rem;
    margin-left: auto;
    margin-right: 1rem;
    background-color: var(--color-primary);
    display: none;
    transition: .3s;
    
    &-cover {
      transform: translateY(-50%);
      cursor: pointer;
      position: absolute;
      top: 0;
      height: 3.2rem;
      width: 3.2rem;
    }

    &::after, &::before {
      transition: .6s;
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      bottom: 0;
      background-color: var(--color-primary);
    }
    
    &::after {
      top: .7rem;
    }

    &::before {
      top: -.7rem;
    }
    
    @media (max-width: 50em) {
      display: block;
    }
  }

  .nav__toggler-input {
    display: none;

    &:checked ~ .nav__toggler {
      background-color: transparent;

      &:before {
        transform: rotateZ(-45deg) translate(0rem, 1rem);
      }
      &:after {
        transform: rotateZ(-135deg) translate(0.1rem, 1.1rem);
      }
    }
    
    &:checked ~ .nav__links {
      transform: translateX(0%);
      opacity: 1;
    }
  }
  
  &__links {
    flex: 1;
    justify-content: space-between;
    display: flex;
    align-items: center;
    margin-left: auto;
    height: 100%;
    
    @media (max-width: 50em) {
      padding-top: 5rem;
      transition: .6s;
      top: var(--nav-height);
      position: fixed;
      left: 0;
      width: 100%;
      height: calc(100vh - var(--nav-height));
      background-image: linear-gradient(var(--color-primary), var(--color-primary-dark));
      opacity: 0;
      transform: translateX(100%);
      display: flex;
      justify-content: flex-start;
      flex-direction: column;
      align-items: center;
      margin-left: auto;
    }
  }
  
  
  &__list {
    margin: auto;
    display: flex;
    height: 100%;
    align-items: center;

    @media (max-width: 50em) {
      flex-direction: column;
      margin: 0;
      flex: 0;
    }
  }
  
  &__item {
    list-style: none;
    height: 100%;
    padding: 0 1.5rem;

    @media (max-width: 50em) {
      height: 5rem;
      margin-top: 2.5rem;
    }
  }
  
  &__link {
    position: relative;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding: 0 1rem;
    
    &:hover {
      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        height: .3rem;
        width: 100%;
        background-color: #555;
      }
    }

    &--selected {
      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        height: .3rem;
        width: 100%;
        background-color: #555;
      }
    }

    @media (max-width: 50em) {
      padding: 0 2rem;

      &:hover {
        &::after {
          bottom: auto;
          z-index: -1;
          width: 150%;
          height: 135%;
          background-color: var(--color-primary-dark);
        }
      }
      
      &:after {
        background-color: rgba(var(--color-primary-light-values), 0.5);
      }
      
      &--selected {
        &:after {
          bottom: auto;
          width: 150%;
          height: 135%;
          z-index: -1;
        }
        
        .nav__icon {
        }
        
        .nav__text {
        }
      }
    }
  }

  &__icon {
    width: 1.5rem;
    height: 1.5rem;
    fill: #555;

    &--bell {
      height: 1.7rem;
      width: 1.7rem;
    }

    &--record {
      margin-right: .5rem;
      fill: #fff;
      
      @media (max-width: 50em) {
        margin-right: 0rem;
      }
    }

    &--carret-down {
      fill: var(--color-secondary);
      height: 2rem;
      width: 2rem;
    }

    @media (max-width: 50em) {
      fill: #eee;
    }
  }
  
  &__text {
    margin-left: 1rem;
    font-size: 1.6rem;
    text-shadow: 0 0 .3px #555;
    letter-spacing: .5px;
    color: #555;

    @media (max-width: 50em) {
      font-size: 2rem;
      color: #eee;
    }
  }
  
  &__auth {
    display: flex;
    
    @media (max-width: 50em) {
      margin-top: 4rem;
      width: 100%;
      height: auto;
      flex-direction: column;
      align-items: center;
    }
  }
  
  &__btn {
    width: 10rem;
    height: 4rem;
    padding-bottom: .2rem;
    text-decoration: none;
    font-size: 1.4rem;
    letter-spacing: .5px;
    display: flex;
    justify-content: center;
    align-items: center;
    
    @media (max-width: 50em) {
      width: 12rem;
      height: 5.5rem;
      padding-bottom: .2rem;
      font-size: 2rem;
      letter-spacing: .3px;
    }

    &--signup {
      background-color: #555;
      text-shadow: 0 0 .5px #ddd;
      color: #ddd;
      
      @media (max-width: 50em) {
        background-color: #fff;
        color: #555;
      }
    }
    
    &--login {
      background-color: #ddd;
      color: #555;
      text-shadow: 0 0 .5px #555;
      margin-left: 1rem;
      
      @media (max-width: 50em) {
        border: 1px solid var(--color-primary);
        color: #ddd;
        background-color: transparent;
        margin-left: 0rem;
        margin-top: 2rem;
      }
    }
  }

  &__loggedIn {
    display: flex;
    align-items: center;
    
    @media (max-width: 50em) {
      width: 100%;
      justify-content: space-around;
    }
  }

  &__loggedIn-btn {
    text-decoration: none;
    background-color: var(--color-secondary);
    height: 4.5rem;
    width: 11rem;
    color: #fff;
    font-weight: 600;
    box-shadow: 0 0 .5rem #ddd;
    border: 2px solid #fff;
    padding: .3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    letter-spacing: .5px;
    margin-right: 3.5rem;
    padding-bottom: 4px;
    border-radius: 10rem;

    @media (max-width: 50em) {
      box-shadow: none;
      margin: 0;
      margin-top: 2rem;
      background-color: transparent;
      border: 1px solid #fff;
      width: 4rem;;
      height: 4rem;
      padding: 1rem;
      position: relative;

      &:hover {
        background-color: #fff;

        .nav__icon--loggedIn {
          fill: #555;
        }
      }
  
      &-text {
        position: absolute;
        bottom: -80%;
        font-weight: 500;
      }
    }

    &--notifications {
      display: none;
      
      @media (max-width: 50em) {
        display: flex;
      }
    }
  }

  &__btn-pseudo {
    position: relative;
    display: flex;
    align-items: center;
    border: none;
    outline: none;
    background-color: transparent;
    cursor: pointer;

    &--bell {
    }
    
    &--user {
      box-shadow: 0 0.2rem 2rem rgb(235, 235, 235);
      padding: .3rem;
      border-radius: 10rem;
      font-size: 1.4rem;
      color: var(--color-secondary);
      letter-spacing: .5px;
    }
    
    @media (max-width: 50em) {
      display: none;
    }     
  }
  
  &__avatar {
    height: 4rem;
    width: 4rem;
    border-radius: 100%;
    object-fit: cover;
    margin-right: 1rem;
  }
}

.nav-user {
  position: relative;

  &-menu {
    position: absolute;
    top: 110%;
    border-radius: 3px;
    right: 0%;
    background-color: #fff;
    width: 15rem;
    display: flex;
    flex-direction: column;
    box-shadow: 0 0 2px #ccc;
    
    &__link {
      padding: 1.5rem;
      width: 100%;
      text-align: center;
      text-decoration: none;
      font-size: 1.6rem;
      color: var(--color-secondary);

      &:not(:last-child) {
        border-bottom: 1px solid #eee;
      }

      &:hover {
        background-color: rgba(238, 238, 238, 0.486);
      }
    }

    &__icon {
      fill: var(--color-secondary);
      margin-bottom: -1px;
    }

    &__text {
      margin-left: 1rem;
    }
  }
  
  @media (max-width: 50em) {
    display: none;
  }
}

.nav-logo {
  height: 100%;
  
  &__link {
    display: flex;
    align-items: center;
    height: 100%;
  }

  &__img {
    height: 55%;
  }
}

.nav-notifications {
  position: relative;
  margin-right: 3.5rem;

  &-count {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    background-color: #ff3031;
    color: #fff;
    width: 2.4rem;
    height: 2.4rem;
    font-size: 1.1rem;
    border: 2px solid #fff;
    top: -95%;
    right: -95%;
    border-radius: 100%;
  }

  @media (max-width: 50em) {
    display: none;
  }
}

.notifications-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 40rem;
  height: 50rem;
  border-radius: 2px;
  background-color: #fff;
  position: absolute;
  right: 100%;
  top: 0%;
  border: 1px solid #ddd;
  // box-shadow: 0 0 5rem #ddd;
  
  &__heading {
    border-bottom: 1px solid #ddd;
    width: 100%;
    padding: 1.6rem;
    text-align: center;
    color: #555;
    font-size: 2rem;
    letter-spacing: .5px;
    margin-bottom: 1rem;
  }
  
  &__list {
    width: 100%;
    flex: 1;
    padding: 0 3rem;
    overflow: auto;
  }
  
  &__item {
    list-style: none;
    width: 100%;
  }
  
  &__link {
    text-decoration: none;
  }
  
  &__bottom-link {
    width: 100%;
    text-align: center;
    padding: 1.8rem;
    border-top: 1px solid #ddd;
    font-size: 1.6rem;
    color: var(--color-secondary);
    text-decoration: none;
  }

  &__error-wrapper {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__error {
    font-size: 1.6rem;
  }
}


.notification {
  display: flex;
  align-items: center;
  padding: 1rem;

  &__user-img {
    height: 4.5rem;
    width: 4.5rem;
    flex-shrink: 0;
    border-radius: 100%;
    background-color: #eee;
  }
  
  &__message {
    margin-left: 2rem;
    font-size: 1.4rem;
    color: #555;
    letter-spacing: .2px;
  }

  &__username, &__title {
    color: var(--color-primary-dark);
    font-size: 1.5rem;
    font-weight: 600;
  }
 
  &__time {
    margin-left: 1rem;
    font-size: 1.2rem;
    color: var(--color-secondary);
    display: flex;
    white-space: nowrap;
  }

  &:hover {
    padding: 1rem;
    background-color: rgba(238, 238, 238, 0.6);
  }
}

.nav-notifications-loader-wrapper {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-notifications-loader {
  border-width: .5rem;
  width: 5rem;
  height: 5rem;
}